<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>
<script>
export default {
  name: 'App',
  async mounted() {
    document.title = 'Golden Tickets';


    if (Object.keys(Telegram.WebApp.initDataUnsafe).length === 0 || typeof Telegram.WebApp.initDataUnsafe.user === 'undefined') {
      console.clear()
      console.log('%cError!', 'font-size:25px;color:red;')
      console.log('%cThis service for Web App Bots in Telegram platform!', 'font-size:16px;color:orange;')
    } else {
      console.clear()
      console.log('%cWarning!', 'font-size:25px;color:orange;')
      console.log('%cYou were able to reach the court, it turns out you are a pro user with us', 'font-size:16px;')
      document.body.setAttribute('app-color-scheme', Telegram.WebApp.colorScheme)
      Telegram.WebApp.onEvent('themeChanged', function(){
        document.body.setAttribute('app-color-scheme', Telegram.WebApp.colorScheme)
      });

      Telegram.WebApp.expand()
      document.body.classList.add(Telegram.WebApp.platform)
      Telegram.WebApp.backgroundColor = "#6F61DD";
      Telegram.WebApp.themeParams.button_color = "#e5ab34";

      await this.$store.dispatch('getUser', Telegram.WebApp.initDataUnsafe.user.id)
      if (!this.$store.state.agreed) {
        this.$router.push('/terms');
      }
    }
  }
}
</script>

<style scoped>
#app {
  display: flex;
  width: 100%;
}
</style>
